const isProduction = process.env.NODE_ENV === 'production';

const BASE_PATH = !isProduction
  ? '/'
  : '/mock-blog/';

export const PATHS = {
  HOME: `${BASE_PATH}`,
  ARTICLE_DETAIL: `${BASE_PATH}article/:postId`,
}

export var linkTo = {
  home: () => PATHS.HOME,
  article: (id: string) => PATHS.ARTICLE_DETAIL.replace(':postId', id),
};
